import UserCenter from '@/components/common/user-center.vue';

import {getStorage} from '@/utils/storage';

export default {
	name: 'freetekHeader',
	props: {
		navs: {
			type: Array,
			default: () => {
				return [];
			}
		}
	},
	data() {
		return {
			// plotlist 需要激活第一个
			// showUserCenter: false,
			nickname: '',
			titlename: '',
			showLogo: false,
			titleSize: 28,
			logoUrl: null,
			isShowFlag: false,
			logoStyle: {
				minWidth: '428px'
			}
		};
	},
	created() {
		this.checkLogin();
		this.showNavs();
		this.refreshPage();
		this.getAreaName();
	},
	mounted() {
		this.$nextTick(() => {
			window.addEventListener('popstate', () => {
				this.refreshPage();
			});
			// this.setFontSize();
		});
	},
	methods: {
		// 设置logo字体大小
		setFontSize() {
			const bodyWidth = document.body.clientWidth;
			const check = bodyWidth > 1366 && bodyWidth <= 1660;
			if (this.titlename.length > 3 && bodyWidth > 1660) {
				this.titleSize = 28;
				return true;
			}
			if (this.titlename.length > 3 && check) {
				this.titleSize = 24;
				return true;
			}
			if (this.titlename.length === 3 && check) {
				this.titleSize = 28;
				return true;
			}
			if (this.titlename.length === 2 && check) {
				this.titleSize = 28;
				return true;
			}
			if (this.titlename.length === 3) {
				this.titleSize = 32;
				return true;
			}
			if (this.titlename.length === 2) {
				this.titleSize = 36;
				return true;
			}
		},

		// 验证是否登录
		checkLogin() {
			if (!getStorage('accessToken')) {
				this.$router.push({
					name: 'login'
				});
				return true;
			}
			const userInfo = getStorage('govGovernmentUserVO');
			this.nickname = userInfo.nickname;
			this.getShowLogo(userInfo.governmentSubjectId);
			this.setLogoUrl();
		},
		showNavs() {
			const flag = getStorage('govGovernmentSubjectVO').showMenuType;
			this.navs = this.navs.filter(nav => {
				return (flag & nav.id) === nav.id;
			});
		},

		// 退出登录
		doLoginOut() {
			this.$ajax
				.post(this.$api.login.logout, {})
				.then(response => {
					const res = response.data;
					if (res.respCode === 0) {
						localStorage.clear();
						sessionStorage.clear();
						setTimeout(() => {
							this.checkLogin();
						}, 500);
						this.$message({
							type: 'success',
							message: '退出成功',
							center: true
						});
					}
				})
				.catch(() => {
					this.$message({
						type: 'error',
						message: '网络异常',
						center: true
					});
				});
		},

		// 切换页面效果
		switchPage({to} = {}) {
			this.$router.push(to);
			this.navs.forEach(item => {
				item.selected = to.name === item.name;
			});
		},

		// 刷新选中不跳
		refreshPage() {
			let routerName = '';
			this.navs.forEach(item => {
				routerName = this.$route.name;
				if (routerName === 'plotlist') {
					routerName = 'landSource';
				}
				item.selected = routerName === item.name;
			});
		},

		// 获取区域名称
		getAreaName() {
			this.titlename = getStorage('govGovernmentSubjectVO').subjectName;
		},

		// show logo
		getShowLogo(id) {
			this.$ajax
				.post(this.$api.login.showLogo, {
					id: id
				})
				.then(({data: res}) => {
					const obj = res.obj;
					if (res.respCode === 0) {
						this.showLogo = !obj.subjectLogoUrl && !!obj.showLogoFlag;
						this.logoUrl = obj.subjectLogoUrl;
						this.isShowFlag = true;
					}
				});
		},

		//上海临时需求 后期可删 修改logo地址
		setLogoUrl() {
			var subjectvo = getStorage('govGovernmentSubjectVO');
			//测试服或本地
			if (window.location.href.indexOf('govtest') !== -1 || window.location.href.indexOf('192.168') !== -1 || window.location.href.indexOf('127.0') !== -1) {
				if ([71, 76].includes(subjectvo.id)) {
					setTimeout(() => {
						$('body .head-logo').css('cursor', 'pointer');
					}, 1000);
					$('body').on('click', '.head-logo', function() {
						window.location.href = 'http://10.121.226.94:81/ncny_fsj';
					});
				}
			}
			//正式服
			if (window.location.href.indexOf('gov.snkoudai') !== -1) {
				/*闵行主体id：49
				 嘉定主体id：57*/
				if ([49, 57].includes(subjectvo.id)) {
					setTimeout(() => {
						$('body .head-logo').css('cursor', 'pointer');
					}, 1000);
					$('body').on('click', '.head-logo', function() {
						window.location.href = 'http://10.121.226.94:81/ncny_fsj';
					});
				}
			}
		}
	},
